@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500&display=swap");
@keyframes fadeIn {
  0% {
    transform: translateY(3rem);
    opacity: 0;
  }
}
.ui-btn {
  display: inline-block;
  padding: 1rem 1.3rem;
  cursor: pointer;
  background: #ff7400;
  color: #fff;
  font-size: 1.7rem;
  overflow: hidden;
  z-index: 1;
  position: relative;
  border-radius: 0.5rem;
  box-sizing: border-box;
  border: 0.2rem solid #2c2c2c;
  text-align: center;
  vertical-align: middle;
}
.ui-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #e5e5e5;
  z-index: -1;
  transition: 0.2s linear;
  clip-path: circle(0% at 0% 5%);
}
@media (any-hover: hover) and (any-pointer: fine) {
  .ui-btn:hover::before {
    clip-path: circle(100%);
  }
}
@media (any-hover: hover) and (any-pointer: fine) {
  .ui-btn:hover {
    color: #2c2c2c;
    border: 0.2rem solid #2c2c2c;
  }
}

.header {
  height: 100vh;
  background-image: url("../../src/img/header.png");
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}
.header__wrapper {
  width: 75%;
  padding: 2rem 12.5% 2rem 12.5%;
}
.header__nav {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
.header__menu ul {
  list-style: none;
  display: flex;
  justify-content: end;
}
.header__menu ul li a {
  color: #fff;
  text-transform: uppercase;
  font-size: 2rem;
  margin: 0 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
}
.header__menu ul li a:hover {
  color: #ff7400;
}
.header__order {
  display: flex;
  align-items: center;
}
.header__order .phone {
  display: flex;
  color: #fff;
  margin-right: 2rem;
  font-size: 1.5rem;
}
.header__order .phone i {
  margin-right: 1rem;
}
.header__title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30%;
  margin: 0 12.5%;
  margin-top: 7%;
}
.header__title .white-text {
  color: #fff;
  position: relative;
  font-size: 2rem;
  text-align: center;
  margin: 2rem 0;
}
.header__title .contrast-text {
  color: #ff7400;
  font-size: 3rem;
  text-align: center;
}
.header__title .contrast-text.b {
  font-size: 4rem;
}
.header__title p {
  width: 100%;
}
.header__title .ui-btn {
  width: 50%;
}
.header .menu__btn {
  display: none;
}
.header .menu__btn i {
  color: #ff7400;
  font-size: 3rem;
}
.header img {
  width: 10%;
}

@media (max-width: 1200px) {
  html {
    font-size: 55%;
  }
  body > header > div > nav > div.header__order > div.phone {
    display: none;
  }
}
@media (max-width: 991px) {
  .header__title {
    margin: 0 35%;
    margin-top: 10%;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    background: #2c2c2c;
  }
  .header__order {
    display: none;
  }
  .header__menu ul {
    flex-direction: column;
    position: absolute;
    left: 0;
    top: 65px;
    background-color: #2c2c2c;
    width: 100%;
    clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  }
  .header__menu ul.active {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
  .header__menu ul li {
    margin: 2rem 0;
  }
  .header__menu ul .cart {
    display: none;
  }
  .header__title {
    margin: 0;
    width: 100%;
    margin-top: 15%;
  }
  .header .menu__btn {
    display: block;
  }
}
@media (max-width: 480px) {
  .header__title {
    margin-top: 50%;
  }
}
footer {
  display: flex;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  text-decoration: none;
  text-transform: capitalize;
  transition: 0.2s linear;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}
html::-webkit-scrollbar {
  width: 1rem;
}
html::-webkit-scrollbar-track {
  background: transparent;
}
html::-webkit-scrollbar-thumb {
  background: #ff7400;
  border-radius: 5rem;
}

.section__wrapper {
  display: flex;
  width: 75%;
  margin: 0 auto;
}

.food-cards {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: -15rem;
  z-index: 2;
}
.food-cards__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 25%;
  padding: 4rem 2rem;
  box-shadow: 0 0.5rem 1.5rem rgba(0, 0, 0, 0.1);
  background: #fff;
}
.food-cards__item img {
  margin-bottom: 2rem;
  width: 30%;
}
.food-cards__title {
  color: #2c2c2c;
  font-size: 2rem;
  text-align: center;
}
.food-cards__title span {
  color: #ff7400;
}
.food-cards__text {
  color: #656363;
  font-size: 2rem;
  text-align: center;
  margin-top: 3rem;
}

@media (max-width: 767px) {
  .food-cards {
    flex-direction: column;
    align-items: center;
  }
  .food-cards__item {
    width: 60%;
    margin-bottom: 4rem;
  }
}